import React, {Component,useState} from "react";
import Modal from "react-modal";
import Button from "@mui/material/Button"
import Papa from "papaparse";

export function Setting(
    {isSettingOpen, setSettingOpen, users, setUsers, gifts, setGifts}
) {
    const customStyles = {
        content: {
          top: "20%",
          left: "50%",
          right: "auto",
          bottom: "auto",
          marginRight: "-50%",
          hight: "70%",
          transform: "translate(-50%, -50%)",
          minWidth: "40%",
        },
    };
    const [tmpUsers, setTmpUsers] = useState([]);
    const [tmpGifts, setTmpGifts] = useState([]);

    const handleFileUsersUpload = (event) => {
        const file = event.target.files[0];
        const reader = new FileReader();
        reader.onload = (event) => {
          const text = event.target.result;
          const result = Papa.parse(text, {
                header: true,
                skipEmptyLines: true,
            });
            console.log(result);
            setTmpUsers(result.data);
        };
        reader.readAsText(file);
    };

    const handleFileGiftsUpload = (event) => {
        const file = event.target.files[0];
        const reader = new FileReader();
        reader.onload = (event) => {
          const text = event.target.result;
          console.log(text);
          const result = Papa.parse(text, {
                header: true,
                skipEmptyLines: true,
            });
            console.log(result);
            setTmpGifts(result.data);
        };
        reader.readAsText(file);
    };

    // 
    return (
        <div>
        <Modal isOpen={isSettingOpen} style={customStyles}>
            <Button variant="outlined" component="label">
                
                <input type="file" hidden 
                onChange={handleFileUsersUpload}
                />
            </Button>  
            <br/> 
            <Button variant="outlined" component="label">
                
                <input type="file" hidden 
                onChange={handleFileGiftsUpload}
                />
                </Button>
            <br/>
            <Button variant="contained" 
                onClick={ () => {
                    // TODO 
                    // TODO 
                    if (users.length === 0) {
                        alert("");
                        return;
                    }
                    if (gifts.length === 0) {
                        alert("");
                        return;
                    }   
                    setUsers(tmpUsers);
                    setGifts(tmpGifts);
                    setSettingOpen(false);
                }}
            ></Button>
        </Modal>
        </div>
    );
}